import "./styles.css";

const rawQuotes = [
  "{This|That} [animal] is so [adjective]!",
  "I wish I could [verb] that [animal].",
  "Aren't [animal]s just so [adjective]?",
  "There must be at least {1-10} [animal]{s} [location]!",
  "THE DRONIES! They are [action]!"
];

const animal = "Dronie";

const adjective = [
  "small",
  "big",
  "cute",
  "sneaky",
  "unusual",
  "helpful",
  "mean"
];

const verb = ["pat", "befriend", "help"];

const location = [
  "on {the|this|that} island",
  "in {the|this|that} valley",
  "{around|in} here",
  "over there",
  "under this {thing|thingie}"
];

const action = [
  "always watching",
  "sneaking out",
  "lurking around",
  "uploading data",
  "unraveling the conspiracy",
  "looking for answers"
];

function getKey(string, count) {
  return string.replace("{", "").replace("}", "").split("|")[
    Math.floor(Math.random() * count)
  ];
}

function extract(string) {
  console.log(string);
  if (string.includes("{the|this|that}")) {
    return string.replace("{the|this|that}", getKey("{the|this|that}", 3));
  }
  if (string.includes("{around|in}")) {
    return string.replace("{around|in}", getKey("{around|in}", 2));
  }
  if (string.includes("{thing|thingie}")) {
    return string.replace("{thing|thingie}", getKey("{thing|thingie}", 2));
  }
  return string;
}

function generateQuote() {
  let quote = rawQuotes[Math.floor(Math.random() * 5)];
  if (quote.includes("{This|That}")) {
    quote = quote.replace("{This|That}", getKey("{This|That}", 1));
  }
  if (quote.includes("[animal]")) {
    quote = quote.replace("[animal]", animal);
  }
  if (quote.includes("[adjective]")) {
    quote = quote.replace(
      "[adjective]",
      adjective[Math.floor(Math.random() * 7)]
    );
  }
  if (quote.includes("[verb]")) {
    quote = quote.replace("[verb]", verb[Math.floor(Math.random() * 3)]);
  }
  if (quote.includes("{1-10}")) {
    let numberOfAnimals = Math.floor(Math.random() * 10);
    numberOfAnimals = numberOfAnimals === 0 ? 1 : numberOfAnimals;
    quote = quote
      .replace("{1-10}", numberOfAnimals)
      .replace("{s}", numberOfAnimals > 1 ? "s" : "");
  }
  if (quote.includes("[location]")) {
    quote = quote.replace(
      "[location]",
      extract(location[Math.floor(Math.random() * 5)])
    );
  }
  if (quote.includes("[action]")) {
    quote = quote.replace("[action]", action[Math.floor(Math.random() * 4)]);
  }
  document.getElementById("quote").innerHTML = quote;
  return quote;
}

document.getElementById("generater").onclick = function () {
  let quote = generateQuote();
  quote = `Quote - "${quote}"\n
  `;
  document.getElementById(
    "share"
  ).href = `https://twitter.com/intent/tweet?original_referer=https://dhruv56.github.io/csb-kn3f7/&ref_src=twsrc%5Etfw%7Ctwcamp%5Ebuttonembed%7Ctwterm%5Eshare%7Ctwgr%5E&text=${quote}%20&url=From https%3A%2F%2Fdhruv56.github.io%2Fcsb-kn3f7%2F&via=DroniesNFT`;
};

document.getElementById("generater").click();
